@charset "utf-8";

// Dimensions
$max-content-width: 800px;

@import
 "variables",
 "themes",
 "layout",
 "base",
 "distill"
;

.partner-item {
 display: flex;
 flex-direction: column;
 align-items: center;
 width: 45%;
 margin-bottom: 20px;
}

.partner-item img {
 max-width: 70%;
 height: auto;
}

.partner-row {
 display: grid;
 grid-template-columns: repeat(2, 1fr);
 grid-gap: 1em;
 align-items: center;
 height: 160px;
}

.partner-logo {
 display: flex;
 justify-content: center;
 align-items: center;
 margin-right: 1em;
}

.partner-name {
 align-items: center;
}

.partner-logo img {
 height: 40px;
 object-fit: contain;
}

.partner-logo .epfl-logo {
 height: 30px;
}

.partner-logo .enpc-logo {
 height: 52px;
}

.partner-logo .ubs-logo {
 height: 45px;
}

.group a {
 display: block;
 margin-bottom: 2px;
}

table {
 width: 100%;
 border-collapse: collapse;
 margin-bottom: 2em;
}

th, td {
 padding: 0.5em;
 text-align: left;
 border-bottom: 1px solid #ddd;
}

th {
 font-weight: bold;
}

.table-responsive {
 max-height: 20vw;
 overflow-y: auto;
}

@media screen and (max-width: 600px) {
 .table-responsive {
 max-height: 40vw;
 }
}
