/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

p, h1, h2, h3, h4, h5, h6, em, div, li, span, strong {
 color: var(--global-text-color);
 text-align: justify;
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table {
 td, th {
 color: var(--global-text-color);
 }
 td {
 font-size: 1rem;
 }
}

a, table.table a {
 color: var(--global-theme-color);
 &:hover {
 color: var(--global-theme-color);
 text-decoration: underline;
 }
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0.5em 10px;
 font-size: 1.2rem;
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
}

// Card

.card {
 background-color: var(--global-card-bg-color);

 img {
 width: 100%;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile

.profile {
 width: 100%;

 .address {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: monospace;
 p {
 display: inline-block;
 margin: 0;
 }
 }
 .imgdate {
 margin-top: -3em;
 height: 3em;
 padding: 0.2em;
 color: white;
 font-weight: bold;
 text-align: right;
 width: 100%;
 font-size: 12px;
 }
}
.profile.align-center{
 margin-left: auto;
 margin-right: auto;
}
.profile.float-right{
 margin-left: 1rem;
}
.profile.float-left{
 margin-right: 1rem;
}

.team-description{
 font-size: 1.2rem;
 // width: 80%;
 margin: auto;
 text-align: justify;
}

@media (min-width: 576px) {
 .profile {
 // width: 80%;
 .address {
 p { display: block; }
 }
 }
}

.post-description {
 margin-bottom: 2rem;
 font-size: 0.875rem;
 a {
